* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: "Segoe UI", Arial, sans-serif;
    line-height: 1.4;
    color: #444;
    background: floralwhite;
    height: 100vh;
}

span {
    margin-left: 10px;
    font-size: 24px;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
}

.inner {
    width: 100%;
    padding: 8rem 10px 4rem;
}

.form-container {
    width: 100%;
    margin-bottom: 20px;
    display: flex;
    border-radius: calc(0.5 * 100px);
    box-shadow: 0 1px 9px 0 rgba(170, 149, 171, 1);
    justify-content: space-evenly;
}

.typebox {
    background-color: floralwhite;
    max-width: fit-content;
}

.input-text {
    font-size: 1rem;
    font-weight: 400;
    width: 85%;
    padding-right: 5px;
    padding-left: 10px;
    background: transparent;
    border-radius: calc(0.5 * 100px);
}

.input-text::placeholder {
    color: #000;
}

.input-submit {
    background: transparent;
    color: #5b5b5b;
    text-transform: capitalize;
    cursor: pointer;
    font-weight: 600;
    margin-right: 10px;
}

.input-text,
.input-submit {
    height: 45px;
    outline: none;
    border: none;
}

.input-submit svg {
    color: #958F9D;
    font-size: 20px;
    margin-top: 2px;
}

.trash {
    background: transparent;
    color: orangered;
    font-size: 18px;
}